<template>
  <div class="page">
    <div class="container">
      <div class="sky-lanterns">
        <mk-sky-lanterns
          ref="skyLanterns"
          class="sky-lanterns-com"
          v-bind="baseConfig"
          @stateChange="stateChange"
          @clickStart="clickStart"
        />
        <div class="info">
          <p>当前状态：{{ state }}</p>
        </div>
      </div>
    </div>
    <demo-block card title="基础操作">
      <button class="common-button primary" @click="reset">全部重置</button>
      <button class="common-button primary" @click="disable">禁用</button>
      <button class="common-button primary" @click="insert">
        塞入特殊状态
      </button>
    </demo-block>
  </div>
</template>
<script>
export default {
  data() {
    return {
      state: 0,
      baseConfig: Object.freeze({
        cardsConfig: {
          img:
            '//yun.tuisnake.com/market-ui/d5f4f929-6513-41a6-af11-37cd647afca2.png',
          length: 9,
        },
        frontImg: 'empty',
      }),
    };
  },

  methods: {
    stateChange(opt) {
      this.state = opt.now;
    },
    async clickStart(index) {
      const skyLanterns = this.$refs.skyLanterns;
      if (!skyLanterns.isWaitStart) {
        console.log('当前不可开始');
        return;
      }
      // 校验抽奖次数
      if (skyLanterns.getCardsState().filter((it) => !it).length <= 2) {
        console.log('抽奖次数已用完');
        return;
      }
      console.log(`您打开的是第${index + 1}个盒子`);
      // 打开对应盒子
      await skyLanterns.start({
        index,
      });
      console.log('打开动画结束');
      await skyLanterns.reset();
    },
    async reset() {
      await this.$refs.skyLanterns.reset({
        needResetAll: true,
      });
    },
    disable() {
      this.$refs.skyLanterns.disable();
    },
    async insert() {
      const cardsState = Array.from({ length: 9 }).fill('');
      cardsState[2] = this.baseConfig.frontImg;
      cardsState[7] = this.baseConfig.frontImg;
      await this.$refs.skyLanterns.reset({
        cardsState,
      });
    },
  },
};
</script>
